import Vue from 'vue'
export default {
    props:{
        options:{
            type:Object,
            required:true
        },
        tagSet:{
            type:Object,
            required:true
        },
        nr:{
            type:Number,
            required:true
        }
    },
    computed:{
        tags(){
            return this.tagSet.values.map(v => v.tag);
        }
    },
    methods:{
        onShow(o){
            this.tagSet.values = this
                                    .tagSet
                                    .values
                                    .filter(v => this
                                                    .options
                                                    .randomizer
                                                    .knownTags
                                                    .indexOf(v.tag)>=0
                                            );
            this.options.randomizer.knownTags.forEach(t => {
                if (this.tagSet.values.find(v => (v.tag==t)) === undefined){
                    this.tagSet.values.push({
                        tag:t,
                        value:''
                    })
                }
            })
        }
    }
}
