export default {
    data:function(){
        return {
            settingsMenu:false,
            highlighted:false,            
            alignments:[
                {
                    label:this.$t('CodeBlockContainer.Start'),
                    value:'left'
                },{
                    label:this.$t('CodeBlockContainer.Center'),
                    value:'center'
                },{
                    label:this.$t('CodeBlockContainer.End'),
                    value:'right'
                }],
            scriptVersions:[
                {
                    label:this.$t('CodeBlockContainer.ScriptVersion_1'),
                    value:'100'
                },{
                    label:this.$t('CodeBlockContainer.ScriptVersion_2'),
                    value:'101'
                }],
            types:[
                {
                    label:this.$t('CodeBlockContainer.Canvas'),
                    value:'PLAYGROUND'
                },{
                    label:this.$t('CodeBlockContainer.Text'),
                    value:'TEXT'
                },{
                    label:this.$t('CodeBlockContainer.Hidden'),
                    value:'BLOCK-hidden'
                },{
                    label:this.$t('CodeBlockContainer.Static'),
                    value:'BLOCK-static'
                },{
                    label:this.$t('CodeBlockContainer.Block'),
                    value:'BLOCK'
                },{
                    label:this.$t('CodeBlockContainer.Blockly'),
                    value:'BLOCKLY'
                }]
        }
    },
    props:{
        block:{
            type:Object,
            required:true
        },
        editMode:{
            type:Boolean,
            default:false
        }
    },
    methods:{
        validNumber(v){
            //console.log(v, isNaN(v), v!='auto');
            if (v!='auto' && isNaN(v)) return "Must be a valid Number or 'auto'."
            return true
        },
        toggleExpanded(){
            this.expanded = !this.expanded;
        },
        moveUp(){
            this.$emit('move-up', this.block.id);
        },
        moveDown(){
            this.$emit('move-down', this.block.id);
        },
        showTypeInfoDialog(){            
            this.$q.dialog({
                title: this.$t('CodeBlockContainer.TypesCaption'),
                message: this.$t('CodeBlockContainer.Types'),
                html: true,
                style:"width:75%",
            }).onOk(() => {
                // console.log('OK')
            }).onCancel(() => {
                // console.log('Cancel')
            }).onDismiss(() => {
                // console.log('I am triggered on both OK and Cancel')
            })                
        },
        removeBlock(){
            const self = this;
            self.highlighted = true;
            this.$q.dialog({
                title: this.$t('CodeBlockContainer.Confirm'),
                message: this.$t('CodeBlockContainer.DeleteQuestion'),
                html: true,
                ok: {
                    push: true,
                    color: 'negative',
                    icon: 'warning'
                },
                cancel: {
                    push: true,
                    color: 'positive'
                },
                persistent: true
            }).onOk(() => {
                this.$emit('remove-block', this.block.id);
            }).onCancel(() => {                
            }).onDismiss(() => {
                self.highlighted = false;                
            })
        }
    },
    computed:{
        expanded:{
            get(){ return this.block.expanded },
            set(v) { this.block.expanded = v }
        },
        canMoveUp(){
            return this.block.id > 0;
        },
        canMoveDown(){
            return !this.block.isLast;
        },
        serializedOptions:{
            get(){
                let obj = {};
                Object
                    .keys(this.block)
                    .filter(k => k.indexOf('$')!=0 && k.indexOf('_')!=0 && k!='obj' && k!='errors' && k!='content' && k!='firstLine' && k!='nextLine' && k!='lineCount' && k!='hasCode' && k!='isLast' && k!='readyCount' && k!='noContent' && k!='uuid' && k!='scopeUUID')
                    .forEach(k => obj[k] = this.block[k])
                
                return JSON.stringify(obj)
            },
            set(v){}
        },
        hasExtendedSettings(){
            return this.type=="PLAYGROUND" || this.type=="BLOCK";
        },
        isVersionedPlayground(){
            return this.type=="PLAYGROUND";
        },
        canSetLineNumbers(){
            return this.type=="BLOCK";
        },
        canHaveAlternativeContent(){
            return this.type=="BLOCK";
        },
        canDefinePlacement(){
            return this.type=="PLAYGROUND";
        },
        shouldAutoReset:{
            get(){
                return this.block.shouldAutoreset;
            },
            set(v){
                this.$emit('auto-reset-change', {
                    shouldAutoreset: v,
                    id:this.block.id
                });
            }
        },
        scriptVersion(){
            if (this.block === undefined || this.block.version === undefined || this.block.version == '')
                return '100';
            return this.block.version
        },
        scriptVersionObj:{
            get(){
                return this.scriptVersions.find(k=>k.value == this.scriptVersion)
            },
            set(v){
                this.$emit('script-version-change', {
                    version: v.value,
                    id:this.block.id
                });
            }
        },
        hasAltComntent:{
            get(){
                return this.block.hasAlternativeContent
            },
            set(v){
                if (v!=this.block.hasAlternativeContent && v){
                    this.$nextTick(()=>{
                        setTimeout(function() {
                            $(".CodeMirror").toArray().forEach(cm =>{            
                                cm.CodeMirror.refresh();
                            });
                        }, 200);
                    });
                }
                this.block.hasAlternativeContent = v
            }
        },
        colorClass(){
            const t = this.type
            if (t == 'TEXT'){
                return 'text-border';
            } else if (t == 'PLAYGROUND'){
                return 'playground-border';
            } else if (t == 'q-t'){
                return 'block-border';
            } else if (t == 'BLOCK-hidden'){
                return 'block-hidden-border'
            } else if (t == 'BLOCK-static'){
                return 'block-static-border'
            }
            return 'default-border'
        },
        bgClass(){
            if (this.highlighted) return 'highlightedCard';
            return '';
        },
        type(){
                if (this.block.type == 'BLOCK'){                
                    if (this.block.hidden) return 'BLOCK-hidden';
                    if (this.block.static) return 'BLOCK-static';
                }
                return this.block.type;  
        },
        typeObj:{
            get(){
                return this.types.find(t => t.value == this.type);                
            },
            set(v){
                v = v.value
                let ret = {
                    type:v.match(/([^-]*)/)[0],
                    hidden:v=='BLOCK-hidden',
                    static:v=='BLOCK-static',                    
                    id:this.block.id
                }
                ret.hasCode = ret.type=='BLOCK'; 
                
                if (ret.type=="PLAYGROUND" && this.block.content==='' && this.editMode){
                    if (this.block.scriptVersion == 100){
                        this.block.content = "{\n    init: function(canvasElement) {\n\n    },\n    update: function(output, canvasElement) {\n\n    }\n}"
                    } else {
                        this.block.content = "{\n    init: function(canvasElement, outputElement, scope) {\n\n    },\n    reset(canvasElement) {},\n    update: function(txt, json, canvasElement, outputElement) {\n\n    }\n}"
                    }                    
                }
                
                this.$emit('type-change', ret);
            }
        },
        visibleLines:{
            get() { return this.block.visibleLines; },
            set(v) { 
                this.$emit('visible-lines-change', {
                    visibleLines: isNaN(v)?v:new Number(v),
                    id:this.block.id
                });
            }
        },
        width:{
            get() { return this.block.width; },
            set(v) { 
                this.$emit('placement-change', {
                    width: v,
                    height:this.block.height,
                    align:this.block.align,
                    id:this.block.id
                });
            }
        },height:{
            get() { return this.block.height; },
            set(v) { 
                this.$emit('placement-change', {
                    width: this.block.width,
                    height:v,
                    align:this.block.align,
                    id:this.block.id
                });
            }
        },align:{
            get() { return this.alignments.find(k => k.value==this.block.align); },
            set(v) { 
                this.$emit('placement-change', {
                    width: this.block.width,
                    height:this.block.height,
                    align:v.value,
                    id:this.block.id
                });
            }
        }
    }
}
