import ScriptBlock from './scriptBlock'
import i18n from '../plugins/i18n'
import Vue from 'vue'
import App from '../App.vue'
import AppEditor from '../AppEditor.vue'
import { uuid } from 'vue-uuid';

import CompilerRegistry from '../lib/CompilerRegistry'
Vue.prototype.$compilerRegistry = CompilerRegistry;



//this will handle the vue mounting on the dom
class CodeBlocksManager {
    constructBlock(data, bl){
        if (this.type == 'PLAYGROUND') {
            if (bl.content=='' || bl.content===undefined || bl.content===null) {
                bl.content = '{}'
            }            
        }
        return new Vue({
            data:function(){return bl;},
            methods:{                
                actualContent(){     
                   if (data.randomizer.active){
                        return Vue.$tagger.replaceRandomTagsInString(this.content, data.randomizer.sets[data.randomizer.previewIndex])                                        
                    } 

                    return this.content
                },
                recreateScriptObject(){                    
                    if (this.type == 'PLAYGROUND') {              
                        const so = new ScriptBlock(this.actualContent(), this.version);
                        this.obj = so
                        console.log("Block Rebuild", this.obj, this._uid);
                    }
                }
            },
            computed:{ 
                isLast(){
                    return this.id == data.blocks.length-1;
                },                                     
                firstLine(){
                    if (this.id == 0) return 1;
                    return data.blocks[this.id-1].nextLine;
                },
                lineCount(){
                    if (!this.hasCode) return 0;
                    return this.content.split('\n').length;
                },
                nextLine(){
                    if (!this.hasCode) return this.firstLine;
                    return this.firstLine + this.lineCount;
                },
                domLibs(){
                    return data.domLibs;
                }
            },
            created(){
                this.recreateScriptObject()
            },
            watch: {
                type: function (newType, oldType) {
                    if (newType!=oldType){
                        this.recreateScriptObject();
                    }
                  }
            }
        })
    }
    constructor(el) {
        this.element = el;
        let data = {
            ...el.dataset,            
            editMode: el.tagName == 'CODEBLOCKSEDITOR' || el.hasAttribute("codeblockseditor"),
            randomizer:{
                active:false,
                previewIndex:0,
                knownTags:[],
                sets:[]
            },
            blocks: []
        };

        if (data.randomizerActive!==undefined){
            data.randomizer.active = data.randomizerActive=='true'||data.randomizerActive=='1';
            delete data.randomizerActive;
        }
        if (data.randomizerPreviewIndex!==undefined){
            data.randomizer.previewIndex = data.randomizerPreviewIndex;
            delete data.randomizerPreviewIndex;
        }
        if (data.randomizerKnownTags!==undefined){
            data.randomizer.knownTags = JSON.parse(data.randomizerKnownTags);
            delete data.randomizerKnownTags;
        }
        if (data.randomizerSets!==undefined){
            data.randomizer.sets = JSON.parse(data.randomizerSets).map((o, i) => {
                let ret = {
                    uuid : uuid.v4(),
                    values: []
                }
                Object.keys(o).forEach(tag => {
                    ret.values.push({
                        tag: tag,
                        value: o[tag]
                    })
                })

                return ret;
            });
            delete data.randomizerSets;
        }


        if (data.question!==undefined){
            data.id = data.question
        }

        if (data.compiler===undefined){
            data.runCode = false
        } else {
            data.compiler = {
                languageType : data.compiler,
                version : data.compilerVersion
            }
            const c = CompilerRegistry.getCompiler({languageType:data.compiler.languageType, version:data.compiler.version});
            if (c===undefined){
                data.runCode = false
                data.language = data.compiler.languageType
            } else {
                data.runCode = data.runCode=='true' || data.runCode=='1';
                data.language = c.language
                data.compiler.version = c.version
            }
            delete data.compilerVersion;        
        }
        
        if (data.domLibs===undefined){
            data.domLibs = []
        } else {
            data.domLibs = JSON.parse(data.domLibs);
        }

        if (data.readonly===undefined){
            data.readonly = false;
        } else {
            if (data.editMode) data.readonly = false;
            else {
                data.readonly = data.readonly=='true' || data.readonly=='1' || data.readonly=='';
            }
        }

        if (data.outputParser===undefined){
            data.outputParser = 'auto';
        } 

        if (data.workerLibs===undefined){
            data.workerLibs = []
        } else {
            data.workerLibs = JSON.parse(data.workerLibs);
        }

        if (data.solutionTheme===undefined){
            data.solutionTheme = 'solarized light';
        } 

        if (data.codeTheme===undefined){
            data.codeTheme = 'xq-light';
        } 

        if (el.hasAttribute('uuid')){
            data.uuid = el.getAttribute('uuid')
        } else {
            data.uuid = uuid.v4()
            el.setAttribute('uuid', data.uuid)
        }

        data.id = Number(data.id);

        if (data.executionTimeout === undefined) data.executionTimeout = 5000
        else data.executionTimeout = Number(data.executionTimeout);

        if (data.maxCharacters === undefined) data.maxCharacters = 1000
        else data.maxCharacters = Number(data.maxCharacters);   
        
        el.querySelectorAll("*").forEach(bl => {
            //only first level children
            if (bl.parentElement !=el ) return;

            let block = {
                ...bl.dataset,
                hasCode: false,
                type: bl.tagName,
                content: bl.textContent,
                id: data.blocks.length,
                uuid:uuid.v4(),
                parentID: data.id,
                width: '100%',
                height: '200px',
                align: 'center',
                readyCount: 0,
                obj: null,  
                toolbox: null,              
                errors:[]
            }

            block.readonly = block.readonly !== undefined && block.readonly != "false" && block.readonly != "0";
            block.static = block.static !== undefined && block.static != "false" && block.static != "0";
            block.hidden = block.hidden !== undefined && block.hidden != "false" && block.hidden != "0";
            block.visibleLines = block.visibleLines === undefined ? 'auto' : block.visibleLines;        

            block.shouldAutoreset = block.shouldAutoreset !== undefined && block.shouldAutoreset != "false" && block.shouldAutoreset != "0";

            block.expanded = block.expanded === undefined || (block.expanded != "false" && block.expanded != "0");
            block.codeExpanded = block.codeExpanded === undefined || ( block.codeExpanded != "false" && block.codeExpanded != "0");

            
            block.noContent = block.noContent !== undefined && block.noContent != "false" && block.noContent != "0";
            if (!data.editMode && block.noContent) {
                block.content = ''
            } 
            if (block.alternativeContent !== undefined  && !block.static && !block.hidden){
                block.hasAlternativeContent = true
                if (!data.editMode && block.noContent){                
                    block.content = block.alternativeContent
                }
            } else {
                block.hasAlternativeContent = false
            }

            if (block.type == 'PLAYGROUND') {                
                block.obj = null;
                
                block.width = bl.getAttribute('width')?bl.getAttribute('width'):block.width
                block.height = bl.getAttribute('height')?bl.getAttribute('height'):block.height
                block.align = bl.getAttribute('align')?bl.getAttribute('align'):block.align   
                block.version = bl.getAttribute('data-version')?bl.getAttribute('data-version'):'101'
            
            } else if (block.type == 'BLOCK') {
                const alts = bl.getElementsByTagName('ALTERNATIVE');
                const codes = bl.getElementsByTagName('CODE');
                if (codes.length > 0){
                    block.content = codes[0].textContent;
                }

                if (alts.length > 0){
                    block.hasAlternativeContent = true
                    block.alternativeContent = alts[0].textContent;
                    if (!data.editMode && block.noContent){                
                        block.content = block.alternativeContent
                    }                    
                } 
                    
                block.hasCode = true;                
            } else if (block.type == 'BLOCKLY') {
                const toolboxes = bl.getElementsByTagName('TOOLBOX');
                let toolbox = undefined;
                if (toolboxes.length > 0){
                    toolbox = toolboxes[0].innerHTML;
                } else {
                    toolbox='';
                }
                block.toolbox = toolbox;

                const codes = bl.getElementsByTagName('CODE');
                if (codes.length > 0){
                    block.content = codes[0].innerHTML;
                }  else {
                    block.content = '<xml xmlns="https://developers.google.com/blockly/xml"></xml>';
                }
                block.hasCode = true;                
            } else if (block.type != 'TEXT') {
                console.log("Skipping", block.type);
                return;
            }

            data.blocks.push(this.constructBlock(data, block));
        })
        this.data = data;   
        
        console.log("DATA", data)
    }

    instantiateVue(){
        const data = this.data;
        const self = this;
        new Vue({
            i18n, 
            render: function (h) {
                const context = {
                    props: {
                        language:data.language,
                        id: data.question,
                        blocks: new Vue({
                            data: function(){ return data;},
                            computed: {},
                            methods: {
                                swap(id1, id2){
                                    const a = this.blocks[id1];
                                    this.blocks[id1] = this.blocks[id2];   
                                    this.blocks[id2] = a;                         
                                    
                                    this.blocks[id1].id = id1;
                                    this.blocks[id2].id = id2;
                                },
                                moveUp(id){
                                    if (id<=0) return;
                                    this.swap(id-1, id);
                                },
                                moveDown(id){
                                    if (id>=this.blocks.length-1) return;
                                    this.swap(id, id+1);
                                },
                                removeBlock(idx){
                                    data.blocks.splice(idx, 1)
                                    for (let i=idx; i<data.blocks.length; i++){
                                        data.blocks[i].id = i;
                                    }
                                },
                                addNewBlock(){
                                    let block = {
                                        hasCode: true,
                                        type: 'BLOCK',
                                        content: "",
                                        id: data.blocks.length,
                                        uuid: uuid.v4(),
                                        parentID: data.id,
                                        expanded: true,
                                        codeExpanded: true,
                                        width: '100%',
                                        height: '200px',
                                        align: 'center',
                                        obj: null,
                                        readonly: false,
                                        static: true,
                                        hidden: false,
                                        version: 101,  
                                        readyCount: 0,              
                                        errors:[],
                                        scopeUUID: data.scopeUUID,
                                        scopeSelector: data.scopeSelector,
                                        visibleLines: 10,
                                        hasAlternativeContent: false,
                                        shouldAutoreset: false,
                                        toolbox: null
                                    }
                                    data.blocks.push(self.constructBlock(data, block));
                                }
                            }
                        })
                    }
                };
                return h(data.editMode?AppEditor:App, context)
            },
        }).$mount(this.element)
    }
}

export default {
    find(scope) {
        if (scope === undefined) scope = document;
        const allCodeBlockParents = scope.querySelectorAll("codeblocks, codeblockseditor, div[codeblocks], div[codeblockseditor]");
        let result = [];
        allCodeBlockParents.forEach(el => {
            const cbm = new CodeBlocksManager(el);            
            let scope = cbm.data.scopeSelector?document.querySelector(cbm.data.scopeSelector):undefined
            if (scope === undefined || scope === null) scope = el;
            
            Vue.$hljs.$vue.processElements(scope);
            if (cbm.data.editMode) {
                Vue.$tagger.processElements(scope);
                cbm.data.scopeSelector = `[uuid=${scope.getAttribute('uuid')}]`
                cbm.data.scopeUUID = scope.getAttribute('uuid');
                cbm.data.blocks.forEach(b => {
                    b.scopeUUID = cbm.data.scopeUUID
                    b.scopeSelector = cbm.data.scopeSelector
                })
            }
            
            result.push(cbm);
        });

        result.mount = function(){
            result.forEach(el => el.instantiateVue());
        }
        return result;
    }
}